.main-nav:active {
    color: rgba(87, 74, 77, 0.911);
    background-color: #19d3da;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
}

.main-nav{
    display: inline-block;
  padding: 10px 25px;
  font-size: 20px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color: #9ed8da;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px #999;
  
}



.main-nav:hover {
    color: rgba(77, 70, 71, 0.911);
    box-shadow: 0 12px 16px 0 rgba(108, 229, 233, 0.774), 0 17px 50px 0 rgba(0,0,0,0.19);
}

