/* BUTTON */

  button {
    margin: 2em auto; 
    -webkit-appearance: none;
    border: 6px solid rgba(255,255,255,0.45);
    border-radius: 50px;
    padding: 1em 3em;
    background-repeat: no-repeat;
    background-size: 100%;
    background-clip: padding-box;
    position: relative;
    color: #FFF;
    cursor: pointer;
  }
  button:before {
    content: "";
    width: 100%;
    height:100%;
   
    top: 5px;
    left: 5px;
    border-radius: 50px;
    z-index: -1;
    -webkit-filter: blur(14px);
            filter: blur(14px);
  }
  button:after {
    content: "";
    width: 100%;
    height:100%;
    top: 5px;
    right: 5px;
    border-radius: 50px;
    z-index: -1;
    -webkit-filter: blur(14px);
            filter: blur(14px);
  }
  
  .button {
    background-image: linear-gradient(90deg, #7b81ec, #3bd1d3);
  }
  .button:before {
    background-image: linear-gradient(90deg, #7b81ec, transparent);
  }
  .button:after {
    background-image: linear-gradient(90deg, transparent, #3bd1d3);
  }
  

