.searchform {
    text-align: center;
    margin: 0;
    border-radius: 10px;
    width: 50%;
    

}
   
  .searchform label, 
  .searchform input {
    color: #3dc6df;
    float: left;
    vertical-align: baseline;
  }
  
  .searchform label { 
    margin-top: 2.3rem;
  font-size: x-large; 
}
  
  .searchform input[type=search] {
    font: 1em/1.618 Open Sans, Arial, Sans-serif;
    border: .125em solid #3dc6df;
    border-width: 0 0 3px;
    background-color: transparent; 
    padding-top: 2.3rem;
  
  }
  
  .searchform input[type=search]:focus {
    border-color: #58e9fc;
    color: #9062fa;
    outline: 0;
  }
  
  @media only screen and (min-width: 48em) {
    .searchform input[type=search]{ width: 70%; }
  }


